import * as React from 'react';
import { Link, graphql, PageProps } from 'gatsby';
import { GatsbyImage, getSrc } from 'gatsby-plugin-image';
import { slugify_util } from '../utils/slugify';
import Bio from '../components/bio';
import Layout from '../components/layout';
import Seo from '../components/seo';
require(`katex/dist/katex.min.css`);

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post },
  location,
}: PageProps<Queries.BlogPostBySlugQuery>) => {
  const siteTitle = site.siteMetadata?.title || `Title`;
  const frontmatter = post.frontmatter;
  const tags = frontmatter.tags || [];
  const featuredImage = frontmatter.featuredImage ? frontmatter.featuredImage.childImageSharp.gatsbyImageData : '';

  const tagsSection =
    tags.length > 0 ? (
      <div className="flex items-center">
        <span className="mr-2">Tags:</span>
        {tags.map((tag) => (
          <Link key={tag} className="p-0 m-0 no-underline" to={`/blog/tag/${slugify_util(tag)}`}>
            <span
              className="flex
              items-center mr-2 mt-2 mb-1 bg-neutral-200 dark:bg-neutral-800 rounded-md
              text-xs uppercase p-1 pl-2 pr-2 border-solid border 
              border-slate-300 hover:border-orange-300
              dark:border-slate-700 dark:hover:border-orange-400"
            >
              {tag}
            </span>
          </Link>
        ))}
      </div>
    ) : (
      ``
    );

  return (
    <Layout location={location} title={siteTitle}>
      <article className="blog-post" itemScope itemType="http://schema.org/Article">
        <header className="mb-4">
          <h1 itemProp="headline" className="mb-1">
            {frontmatter.title}
          </h1>
          <small>
            {frontmatter.date} &bull; {post.fields.timeToRead.text}
          </small>
        </header>
        {featuredImage ? (
          <GatsbyImage image={featuredImage} alt={frontmatter.title + ' - Featured image'} className="mb-4" />
        ) : (
          ''
        )}

        <section dangerouslySetInnerHTML={{ __html: post.html }} itemProp="articleBody" />

        {tagsSection}

        <hr className="mt-8 mb-8" />
        <footer>
          <Bio />
        </footer>
        <hr className="mt-8 mb-8" />
      </article>

      <nav className="mt-8 mb-8">
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  );
};

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
      image={getSrc(post.frontmatter.featuredImage?.childImageSharp?.gatsbyImageData)}
    />
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($id: String!, $previousPostId: String, $nextPostId: String) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      fields {
        timeToRead {
          text
        }
      }
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        featuredImage {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, quality: 90)
          }
        }
        tags
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`;
